.openTeam {
  width: 100%;
  border-spacing: 0;
}
.teamHeader {
  background-color: rgba(223, 217, 217, 0.527);
}

.row {
  height: 40px;
}
.winHeaderCell {
  color: rgb(89, 89, 236);
}
.loseHeaderCell {
  color: rgb(243, 76, 76);
}
.winContents {
  background-color: rgb(215, 215, 240);
  border: 1px solid rgb(161, 161, 248);
}
.loseContents {
  background-color: rgb(243, 205, 205);
  border: 1px solid rgb(241, 134, 134);
}
