.matche {
  display: flex;
  flex-direction: column;
}
.matchWin {
  display: flex;
  height: 150px;
  width: 100%;

  background-color: rgb(177, 177, 241);
}
.matchLose {
  display: flex;
  height: 150px;
  width: 100%;
  background-color: rgb(228, 181, 181);
}
.reMatch {
  display: flex;
  height: 150px;
  margin-bottom: 10px;
  background-color: rgb(212, 209, 209);
}
.gameStats {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  width: 80px;
  color: #555555;
}
.gameResultWin {
  color: rgb(13, 13, 143);
}
.gameResultLose {
  color: rgb(116, 7, 7);
}
.gameRestart {
  color: black;
}

.gameSettingInfo {
  display: grid;
  place-items: center;
  place-content: center;
  grid-template-columns: 45px 25px 25px;

  grid-gap: 3px;
}
.championImg {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.championName {
  grid-column: 1/4;
  font-size: 12px;
  color: #555555;
}
.kdaBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80px;
}
.grade {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 12px;
}
.kda {
  margin-bottom: 10px;
}
.stats {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90px;
}
.controlWard {
  font-size: 12px;
}
.items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 130px;
}
.itemList {
  display: grid;
  grid-template-columns: repeat(4, 30px);
  grid-template-rows: repeat(2, 30px);
  grid-gap: 3px;
}

.followPlayers {
  display: flex;
  margin-left: 10px;
  width: 200px;
}
.summoner {
  display: grid;
  grid-template-columns: 18px 82px;
  grid-template-rows: 18px;
  padding-bottom: 3px;
}
.blueTeam,
.redTeam {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100px;
}
.summonerChampionName {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  font-size: 12px;
  padding-left: 5px;
}
.summonerChampionName a {
  text-decoration-line: none;
}
.summonerLink {
  color: #555555;
}
.statsBtn {
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-size: 22px;
}
.winStatsBtn {
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-size: 22px;
  background-color: rgb(151, 153, 243);
}
.loseStatsBtn {
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-size: 22px;
  background-color: rgb(238, 125, 125);
}
.reStatsBtn {
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-size: 22px;
  background-color: rgb(133, 130, 130);
}
.runeSpell {
  display: grid;
  grid-template-columns: repeat(2, 20px);
  grid-template-rows: repeat(2, 20px);
  margin-left: 15px;
  column-gap: 1px;
  row-gap: 1px;
}
