.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  background-color: rgb(250, 250, 240);
}

.contents {
  display: flex;
  width: 1000px;
  border: 1px solid gold;
  border-radius: 10px;
  padding: 10px;
  background-color: beige;
}
.error {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}
